import { token } from '@/tools'
export default {
  data() {
    return {
      // sign up
      account: '',
      password: ''
    }
  },
  computed: {
    isSign() {
      return !(this.account && this.password)
    }
  },
  methods: {
    relogin() {
      this.$callApi({
        url: 'api/mobile/login/doLogin',
        method: 'post',
        data: {
          account: this.account,
          password: this.password
        }
      }).then(data => {
        // console.log(this.$color.warning)
        this.$notify({
          message: '登录成功',
          duration: 1000,
          background: '#67c23a'
        })
        console.log(data)
        token.setToken(data.user.token)
        this.onPush('layout')
      })
    },
    onPush(routerName) {
      this.$router.push({ name: routerName })
    }
  }
}
